li.item {
	width: 25vh;
    height: 25vh;
    min-height: 80px;
    min-width: 80px;
    margin: 2.5vh;
    display: inline-block;
    background-position: center;
    background-size: contain;
}

li.item span {
	color: white;
	position: relative;
	top:0.5em;
	text-shadow: black 0 0 0.1em, black 0 0 0.1em, black 0 0 0.1em;
	max-width: 80px;

	-webkit-user-select: none;  
  	-moz-user-select: none;     
  	-ms-user-select: none;      
  	user-select: none;          
}

.item.on {
	outline: 0.5rem solid #333333;
	-webkit-transition-property: outline;
	-o-transition-property: outline;
	transition-property: outline;
	-webkit-transition-duration: 0.5s;
	     -o-transition-duration: 0.5s;
	        transition-duration: 0.5s;
	-webkit-transition-timing-function: linear;
	     -o-transition-timing-function: linear;
	        transition-timing-function: linear;
}

.item.off {
	outline: none;
	-webkit-transition-property: outline;
	-o-transition-property: outline;
	transition-property: outline;
	-webkit-transition-duration: 0.5s;
	     -o-transition-duration: 0.5s;
	        transition-duration: 0.5s;
	-webkit-transition-timing-function: linear;
	     -o-transition-timing-function: linear;
	        transition-timing-function: linear;
}

@media (max-height: 650px) {

	li.item span {
		font-size:2em;
	}
}

@media (min-height: 650px) and (max-height: 850px) {

	li.item span {
		font-size:2.5em;
	}
}

@media (min-height: 851px) and (max-height: 5200px) {

	li.item span {
		font-size:3em;
	}
}


